<template>
	<div>
		<button type="button" @click="subNumber" class="opt-button-sub">-</button>
		<input type="text" v-model="rVal" :placeholder="placeholder"/>
		<button type="button" @click="addNumber" class="opt-button-add">+</button>
	</div>
</template>
<script type="text/javascript">
export default{
	data(){
		return {
			rVal:"",
		}
	},
	  props:{
	      pars:{
	        type:Object,
	        default:function() {
	        return {
	          toFixedNumber:0,
	          minNumber:0,
	          maxNumber:10000000,
	          unitNumber:1,
	          val:""
	        };
	        }
	      },
	      placeholder:{
	      	type:String,
	      	default:"请输入数量2"
	      }
    },
    watch:{
    	pars:function(v){
    		this.rVal=v.val;
    	},
    	rVal(v){
    		if(this.pars.val!=v){
    			this.pars.val=v;
    			this.$emit("callback",this.pars.val)
    		}

    	}

    },
    methods:{
    	subNumber:function(){
    		var v=this.pars.val*1-this.pars.unitNumber*1;
    		v=v>this.pars.minNumber?v:this.pars.minNumber;
    		v=v>this.pars.maxNumber?this.pars.maxNumber:v;
    		this.rVal=(v*1).toFixed(this.pars.toFixedNumber)
    	},
    	addNumber:function(){
    		var v=this.pars.val*1+this.pars.unitNumber*1;
    		v=v<this.pars.maxNumber?v:this.pars.maxNumber;
    		v=v<this.pars.minNumber?this.pars.minNumber:v;
    		this.rVal=(v*1).toFixed(this.pars.toFixedNumber)
    	},
    }
}
</script>



