<template>
<transition name="slide-left-menu">
  <div class="slidemenu" v-show="visible" >
    <div class="slidemenu-conent" >
      <custHeader :title="title"></custHeader>
      <div class="login-tt">
        <span>剩余数量&nbsp;:&nbsp;{{sysl}}</span>

      </div>
      <div class="login-tt">
        <span>最大可买&nbsp;:&nbsp;{{zdkm}}</span>

      </div>
      <div class="login-tt">
        <span>申购单价&nbsp;:&nbsp;{{orderPrice | toMoney}}</span>
      </div>
      <div class="login-tt">
        <span>申购上限&nbsp;:&nbsp;{{maxLimitQty }}</span>
      </div>
      <div class="login-tt">
        <span>申购下限&nbsp;:&nbsp;{{minLimitQty }}</span>
      </div>
      <div class="login-tt">
        <span>梯度&nbsp;:&nbsp;{{baseQty }}</span>
      </div>
      <div class="login-inputs">
        <!-- <mt-field label="数量" placeholder="请输入数量" type="orderQty" v-model="orderQty"></mt-field> -->
        <div>
            <!-- 数量: -->
        </div>
            <numberBox :pars="numPars" placeholder="输入认购数量" class="opt-button pay-btn"   v-model="orderQty" ></numberBox>
        <div class="login-btns">
          <mt-button type="danger" size="large" @click="buy">购买</mt-button>
        </div>
      </div>
    </div>
  </div>
</transition>
</template>

<script>
import {
  Field,
  Button
} from 'mint-ui';
import cookieUtil from 'js-cookie'

import numberBox from 'components/numberBox.vue'
import custHeader from  'components/custHeader'
import {
  MessageBox
} from 'mint-ui'
Vue.component(Button.name, Button);
Vue.component(Field.name, Field);
export default {
  name: 'slidemenu',
  data() {
    return {
        numPars:{
        toFixedNumber:0,
         minNumber:1,
          maxNumber:1000000,
          unitNumber:1,
          val:""
        },
      title:"认购付款",
      visible: true,
      cust: '',
      stkCode: '',
      baseQty:'',
      orderPrice: '',
      orderQty: '',
      kgje: '',
      zdkm:'',
      sysl:'',
      maxLimitQty:'',
      minLimitQty:'',
      stkBiz:global.STKBIZ.SUBSCRIBE   ,   //资金申购  默认这个  不做认购申购
    }
  },
  computed: {

  },
  components:{
    custHeader,
    numberBox
  },
  created: function() {
    if(this.$route.query.stkBiz){
      this.stkBiz=this.$route.query.stkBiz;
     }
  
    if (this.$route.query.stkCode) {

      this.stkCode = this.$route.query.stkCode
    }
    if (this.$route.query.issPrice || this.$route.query.issPrice==0) {
      this.orderPrice = this.$route.query.issPrice
    }
    if (this.$route.query.kgje || this.$route.query.kgje==0) {
      this.kgje = this.$route.query.kgje
    }
    if (this.$route.query.sysl || this.$route.query.sysl==0) {
      this.sysl = this.$route.query.sysl
    }
    if (this.$route.query.maxLimitQty || this.$route.query.maxLimitQty==0) {
      this.maxLimitQty = this.$route.query.maxLimitQty
    }
    if (this.$route.query.minLimitQty || this.$route.query.minLimitQty==0) {
      this.minLimitQty = this.$route.query.minLimitQty
    }
    if (this.$route.query.baseQty) {
        this.baseQty=this.$route.query.baseQty
      this.numPars = Object.assign( this.numPars,{unitNumber:this.$route.query.baseQty*1})
  }
  this.getMaxQty();
  this.numPars={
  toFixedNumber:0,
   minNumber:this.minLimitQty,
    maxNumber:this.sysl,
    unitNumber:this.baseQty,
    val:this.minLimitQty
};
  },
  watch: {
    value(val) {
      this.visible = val.isShow;
    },
    visible(val) {
      this.value.isShow = true;
    }
  },
  methods: {
      getMaxSl:function(){
        var num=this.sysl>this.zdkm?this.zdkm:this.sysl;
        if((num*1)%(this.baseQty*1)!=0){
          num=Math.floor( (num*1)/(this.baseQty*1))*(this.baseQty*1)
        }
          return num;
      },
      getMaxQty:function(val){//求最大申报数量
          //console.log(val)
          var data={
              stkex:global.scdm,
              stkbd:global.stkbd,
              orderPrice:this.orderPrice ,
              stkBiz:global.STKBIZ.SUBSCRIBE_PURCHASE,
              trdacct:cookieUtil.get("trdAcct"),
              stkCode:this.stkCode
          }
          axiosUtil.upCltBuyMaxQtyQry(data).then(data=>{

                  var data=data.data;

                  if (data.errorcode == 0) {
                     this.zdkm =data.data;
                     var maxNum=this.getMaxSl();
                     this.zdkm=maxNum;
                     this.numPars={
                     toFixedNumber:0,
                      minNumber:this.minLimitQty,
                       maxNumber:maxNum,
                       unitNumber:this.baseQty,
                       val:this.minLimitQty
                   };
                  } else {
                      MessageBox("提示",data.errormsg);
                      this.zdkm =0;
                       var maxNum=this.getMaxSl();
                       this.zdkm=maxNum;
                     this.numPars={
                     toFixedNumber:0,
                      minNumber:0,
                       maxNumber:0,
                       unitNumber:0,
                       val:0
                   };

                  }
          })
          },
    goBack: function() {
      this.$router.go(-1);
    },
    buy: function() {
       if(this.numPars.val==""){
          MessageBox("提示","数量不准为空!");
          return ;
      }

       if(this.numPars.val*1>this.zdkm || this.numPars.val*1<this.minLimitQty){
          MessageBox("提示","数量不能大于最大可买("+this.zdkm +")，不能小于申报下限("+this.minLimitQty+")");
          return ;
      }
      if((this.numPars.val*1)%this.baseQty!=0){
          MessageBox("提示","数量必须是买量单位("+this.baseQty+")的整数倍");
          return ;
      }
      var data = {
        stkex: global.scdm , //交易市场
        stkbd: global.stkbdApply,  //交易板块
        stkCode:this.stkCode, //证券代码
        currency:0, //币种类型 人民币
        orderPrice: this.orderPrice,
        orderQty: this.numPars.val, //数量
        trdacct: cookieUtil.get("trdAcct"),
        stkBiz: this.stkBiz, //业务类型//资金申购  416认购申购
      }
      axiosUtil.entrust(data).then(data=>{
          var data=data.data;
           if (data.errorcode == 1) {
               MessageBox("提示", data.errormsg)
           }else{
               MessageBox("提示",  "委托成功，委托号=" + data.data)
           }
      })
    //   axiosUtil.insert(data).then(function(data) {
    //     var data = data.data;
    //     if (data.errorcode == 1) {//登录判断
    //       MessageBox("提示", data.errormsg)
    //     } else {
    //       var orderSnos = data.data;
    //       axiosUtil.order({
    //         orderSnos: orderSnos
    //       }).then(function(data) {
    //         var data = data.data;
    //         if (data.errorcode == 1) {
    //           MessageBox("提示", data.errormsg)
    //         } else {
    //           MessageBox("提示", "委托成功，委托号=" + data.data);
    //         }
    //       })
    //     }
    //   });
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.slidemenu {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
  left: 0;
  top: 0;
  background-color: #efeee9;
  transition: .5s all ease;
}

.slidemenu-conent {
  height: 100%;
  width: 100%;
  background: #efeee9;
}

.menu-list {
  color: #111;
}

.login-tt {
  margin: 8px 26px;
  color: #999;
  font-size: 12px;
}

.login-inputs {
  width: 100%;
  margin: 0 auto;
}
.pay-btn{
    margin: 12px 20px;
}
.login-btns {
  width: 90%;
  margin: 12px auto;
}

.login-ghost {
  background: rgba(0, 0, 0, 0);
  border: 1px solid #ef4f4f;
  color: #ef4f4f;
  margin-top: 12px;
}

.login-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  height: 45px;
  line-height: 45px;
  font-size: 19px;
  background: #ef4f4f;
  color: #ffffff;
  text-align: center;
}

.top-back {
  max-width: 85px;
  float: left;
}
</style>



